___CSS_LOADER_EXPORT___.push([module.id, `.product-card {
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 8px; /* Reduced padding */
  margin: 8px; /* Reduced margin */
  text-align: center;
  background-color: #fff;
  transition: transform 0.3s, box-shadow 0.3s;
  width: 100%;
  max-width: 300px; /* Reduced max-width */
  margin: 1% !important;
}

.product-card.dark {
  border: 1px solid #000;
  background-color: #444;
}

.product-card:hover {
  transform: translateY(-5px); /* Reduced translateY */
  cursor: pointer;
}

.media {
  width: 100%;
  max-width: 100%;
  height: auto;
  border-radius: 8px;
}

.media.dark {
  background: #333;
}

.media.light {
  background: #eeeeee;
}

.skeleton-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.skeleton-image {
  width: 100%;
  height: 150px; /* Reduced height */
  margin-bottom: 8px;
  background-color: #ccc;
  border-radius: 8px;
}

.skeleton-text {
  width: 60%;
  height: 20px;
  background-color: #ccc;
  border-radius: 4px;
}

h2 {
  font-size: 1.2em; /* Reduced font-size */
  margin: 0.5em 0;
}

p {
  margin: 0.5em 0;
  font-size: 0.9em; /* Reduced font-size */
}

button {
  padding: 8px 16px; /* Reduced padding */
  margin: 5px; /* Reduced margin */
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9em; /* Reduced font-size */
}

button:hover {
  background-color: #ddd;
}

@media (max-width: 768px) {
  .product-card {
    margin: 8px;
    padding: 8px;
  }

  .media {
    max-height: 200px; /* Reduced max-height */
    object-fit: cover;
  }
}

@media (min-width: 769px) {
  .product-card {
    margin: 16px auto;
    padding: 8px;
  }

  .media {
    max-height: 250px; /* Reduced max-height */
    object-fit: contain;
  }
}
